<script>
import axios from '@/axios-auth'
import Chart from 'chart.js'

export default {

  data () {
    return {
      noScore: true,
      chartId: 'subject-chart',
      subjectChartIsHidden: false,
      overallChartIsHidden: true,
      firstName: null,
      lastName: null,
      data_is_loaded: false,
      profile: {
        totalQuestionsCorrect: 0,
        totalQuestionsTaken: 0,
        totalTraumaQuestionsCorrect: 0,
        totalTraumaQuestionsAttempted: 0,
        totalMedicalQuestionsCorrect: 0,
        totalMedicalQuestionsAttempted: 0,
        totalAppendeciesQuestionsCorrect: 0,
        totalAppendeciesQuestionsAttempted: 0,
        totalHazMatQuestionsAttempted: 0,
        totalHazMatQuestionsCorrect: 0,
        totalCardiacArrestQuestionsCorrect: 0,
        totalCardiacArrestQuestionsAttempted: 0,
        totalGOPQuestionsAttempted: 0,
        totalGOPQuestionsCorrect: 0,
        examsCompleted: 0,
        weakestSubjectDisplay: '',
        strongestSubjectDisplay: ''
      }
    }
  },
  created () {
    // axios.get('api/sessions/', {
    console.log('created')
    console.log(localStorage.token)
    console.log('user token ' + this.$store.state.auth_token)

    console.log(this.$store.state.isAuthenticated)

    axios.get('api/account/user-page/', {
      headers: { Authorization: 'Token ' + localStorage.token }
    })
      .then(res => {
        const data = res.data
        this.firstName = data.user.first_name
        this.lastName = data.user.last_name
        this.profile.totalQuestionsTaken = data.total_overall_questions_attempted
        this.profile.totalQuestionsCorrect = data.total_overall_questions_correct
        this.profile.totalMedicalQuestionsCorrect = data.total_medical_questions_correct
        this.profile.totalMedicalQuestionsAttempted = data.total_medical_questions_attempted
        this.profile.totalTraumaQuestionsCorrect = data.total_trauma_questions_correct
        this.profile.totalTraumaQuestionsAttempted = data.total_trauma_questions_attempted
        this.profile.totalAppendeciesQuestionsCorrect = data.total_appendecies_questions_correct
        this.profile.totalAppendeciesQuestionsAttempted = data.total_appendecies_questions_attempted
        this.profile.totalHazMatQuestionsCorrect = data.total_hazardous_materials_questions_correct
        this.profile.totalHazMatQuestionsAttempted = data.total_hazardous_materials_questions_attempted
        this.profile.totalCardiacArrestQuestionsAttempted = data.total_cardiac_arrest_questions_attempted
        this.profile.totalCardiacArrestQuestionsCorrect = data.total_cardiac_arrest_questions_correct
        this.profile.totalGOPQuestionsCorrect = data.total_gop_questions_correct
        this.profile.totalGOPQuestionsAttempted = data.total_gop_questions_attempted
        this.profile.examsCompleted = data.exams_completed
        this.profile.weakestSubjectDisplay = data.weakest_subject
        this.profile.strongestSubjectDisplay = data.strongest_subject
        this.data_is_loaded = true
        if (data.total_overall_questions_correct > 0) {
          this.noScore = false
          this.createSubjectBreakdownChart('subject-chart')
          this.createOverallBreakdownChart('overall-chart')
        }
        localStorage.firstName = this.firstName
      })
      .catch(err => console.log(err))
  },
  methods: {
    changeChartIdToSubject () {
      this.chartId = 'subject-chart'
    },
    changeChartIdToOverall () {
      this.chartId = 'overall-chart'
    },
    createSubjectBreakdownChart (chartId) {
      console.log('test data => ' + this.profile.totalMedicalQuestionsCorrect)
      const ctx = document.getElementById(chartId)
      // eslint-disable-next-line no-unused-vars
      const myChart = new Chart(ctx, {
        type: 'doughnut',
        data: {
          labels: ['Trauma', 'Medical', 'Appendices', 'Hazardous Materials', 'Cardiac Arrest', 'GOP'],
          datasets: [
            {
              data: [
                this.profile.totalTraumaQuestionsCorrect,
                this.profile.totalMedicalQuestionsCorrect,
                this.profile.totalAppendeciesQuestionsCorrect,
                this.profile.totalHazMatQuestionsCorrect,
                this.profile.totalCardiacArrestQuestionsCorrect,
                this.profile.totalGOPQuestionsCorrect
              ],
              backgroundColor: [
                'rgb(246,7,7)', // Red
                'rgba(7,126,248,0.5)',
                'rgb(238,201,54)',
                'rgb(116,36,233)',
                'rgb(7,3,3)',
                'rgb(71,245,11)'
              ],
              label: 'Score Breakdown'
            }
          ]
        },
        options: {
          responsive: true,
          legend: {
            position: 'bottom'
          },
          title: {
            display: true,
            text: ''
          },
          animation: {
            animateScale: true,
            animateRotate: true
          }
        }
      })
    },
    createOverallBreakdownChart (chartId) {
      console.log('test data => ' + this.profile.totalMedicalQuestionsCorrect)
      // eslint-disable-next-line no-unused-vars
      const questionsWrong = this.profile.totalQuestionsTaken - this.profile.totalQuestionsCorrect

      const ctx = document.getElementById(chartId)
      // eslint-disable-next-line no-unused-vars
      const myChart = new Chart(ctx, {
        type: 'pie',
        data: {
          labels: ['Correct', 'Wrong'],
          datasets: [
            {
              data: [
                this.profile.totalQuestionsCorrect,
                questionsWrong
              ],
              backgroundColor: [
                // 'rgb(99,246,7)', // Red
                'rgb(163, 211, 156)',
                'rgb(255, 64, 71)'
                // 'rgb(245,11,19)'
              ],
              label: 'Score Breakdown'
            }
          ]
        },
        options: {
          responsive: true,
          legend: {
            position: 'bottom'
          },
          title: {
            display: true,
            text: ''
          },
          animation: {
            animateScale: true,
            animateRotate: true
          }
        }
      })
    }
  },
  computed: {
    overallData: function () {
      return {
        labels: ['Trauma', 'Medical', 'Appendices', 'Hazardous Materials', 'Cardiac Arrest', 'GOP'],
        datasets: [
          {
            data: [
              (this.profile.totalTraumaQuestionsCorrect / this.profile.totalTraumaQuestionsAttempted),
              this.profile.totalMedicalQuestionsCorrect / this.profile.totalMedicalQuestionsAttempted,
              this.profile.totalAppendeciesQuestionsCorrect / this.profile.totalAppendeciesQuestionsAttempted,
              this.profile.totalHazMatQuestionsCorrect / this.profile.totalHazMatQuestionsAttempted,
              this.profile.totalCardiacArrestQuestionsCorrect / this.profile.totalCardiacArrestQuestionsAttempted,
              this.profile.totalGOPQuestionsCorrect / this.profile.totalGOPQuestionsAttempted
            ],
            backgroundColor: [
              'rgb(246,7,7)', // Red
              'rgba(7,126,248,0.5)',
              'rgb(238,201,54)',
              'rgb(116,36,233)',
              'rgb(7,3,3)',
              'rgb(71,245,11)'
            ],
            label: 'Score Breakdown'
          }
        ]
      }
    }
  }
}

</script>

