<template>
  <section class="about">
    <div class="about__wrapper">
        <p class="about__description-main">You may be asking yourself why us</p>
        <p class="about__description-secondary">
              This website and content have been curated by a select
            group of New York City Paramedics.
            We have
            over 45 years of combined experience
            working on the streets of NYC.
            Each of us have
            been in your exact same position,
            eager to advance our career and nervous about whether
            or not we studied enough.
            When you choose to use us as your study aide and resource you are getting
            original, tailor made, content
            specific for the exams you are preparing to take. We believe our content is the best on the
            market for anyone looking to pass the exams we cover.
        </p>
    </div>
    <div class="about__wrapper-two">
      <div class="about__contact">
        <img v-if="loading" src="../assets/star-of-life-mod.png" alt="loading-image" class="loading">
        <div v-else-if="success" class="success">
          <img src="../assets/check-mark.png" alt="check-image" class="success__img">
          <p class="about__contact-text">Submitted</p>
        </div>
        <p  v-else-if="error" class="about__contact-text error">Try again</p>
        <p  v-else class="about__contact-text">Contact Us</p>
      </div>
      <form @submit.prevent="submit" class="about__form">
        <div class="about__user-field">
          <div class="about__form-row">
            <label v-if="nameFilled" for="name" class="about__label">Name</label>
            <label v-if="!nameFilled" for="name" class="about__label">
              Name
              <span class="not-filled">*required</span>
            </label>
            <input type="text" class="about__input" id="name" v-model.trim="name">
          </div>
          <div class="about__form-row">
            <label v-if="emailFilled" for="email" class="about__label">Email</label>
            <label v-if="!emailFilled" for="name" class="about__label">
              Email
              <span class="not-filled">*required</span>
            </label>
            <input type="email" class="about__input" id="email" v-model.trim="email">
          </div>
        </div>
        <div class="about__form-comment">
          <label v-if="commentFilled" for="comment" class="about__label">Comment</label>
          <label v-if="!commentFilled" for="name" class="about__label">
            Comment
            <span class="not-filled">*required</span>
          </label>
          <textarea class="about__text-area" name="comment" id="comment" cols="30" rows="5" maxlength="500" v-model.trim="comment"></textarea>
        </div>
        <div class="about__form-button">
          <button class="about__button">Submit</button>
        </div>
      </form>
    </div>
    <div class="margin"></div>
  </section>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      loading: false,
      success: false,
      error: false,
      name: '',
      email: '',
      comment: '',
      date: '',
      nameFilled: true,
      emailFilled: true,
      commentFilled: true
    }
  },
  watch: {
    name () {
      if (this.name !== '') {
        this.nameFilled = true
        this.success = false
      }
    },
    email () {
      if (this.email !== '') {
        this.emailFilled = true
        this.success = false
      }
    },
    comment () {
      if (this.comment !== '') {
        this.commentFilled = true
        this.success = false
      }
    }
  },
  methods: {
    submit () {
      if (this.name === '') {
        this.nameFilled = false
      } else if (this.email === '') {
        this.emailFilled = false
      } else if (this.comment === '') {
        this.commentFilled = false
      } else {
        this.loading = true
        axios.post('https://first-responder-front-end.herokuapp.com/api/contact/feedback/', {
          name: this.name,
          email: this.email,
          comment: this.comment,
          date: new Date()
        })
          .then(() => {
            this.loading = false
            this.error = false
            this.success = true
            this.nameFilled = true
            this.emailFilled = true
            this.comment = true
            this.name = ''
            this.email = ''
            this.comment = ''
            console.log('posted!' + new Date())
          })
          .catch(() => {
            this.loading = false
            this.error = true
          })
      }
    }
  }
}
</script>
