<template>
  <div class="procedure__text">
    <p class="center">Respiratory Therapy</p>
    <button @click="nc" class="procedure__text-style">Nasal Cannula</button>
    <button @click="oxygenNrb" class="procedure__text-style">Nonrebreather</button>
    <button @click="bvm" class="procedure__text-style"> Ventilate with BVM</button>
    <button @click="intubate" class="procedure__text-style">Intubate</button>
    <button @click="cpap" class="procedure__text-style">CPAP</button>
  </div>
</template>

<script>
export default {
  emits: ['nasal-cannula', 'non-rebreather', 'bvm-mask', 'tube', 'cpap-mask'],
  data () {
    return {
    }
  },
  methods: {
    nc () {
      this.$emit('nasal-cannula')
    },
    oxygenNrb () {
      this.$emit('non-rebreather')
    },
    bvm () {
      this.$emit('bvm-mask')
    },
    cpap () {
      this.$emit('cpap-mask')
    },
    intubate () {
      this.$emit('tube')
    }
  }
}
</script>

