<script>
import ScenarioNav from '@/components/scenario/ScenarioNav'
import ScenarioVitalSignes from '@/components/scenario/ScenarioVitalSignes'
import DispatchInfo from '@/components/scenario/DispatchInfo'
import PtAssessment from '@/components/scenario/PtAssessment'
import Procedure from '@/components/scenario/Procedure'
import CardIntervention from '@/components/scenario/CardIntervention'
import Medication from '@/components/scenario/Medication'
import LogList from '@/components/scenario/LogList'
import Respiratory from '@/components/scenario/Respiratory'
// import ScenarioScore from '@/components/scenario/ScenarioScore'
import StrikeCounter from '@/components/scenario/StrikeCounter'
import AlertBar from '@/components/scenario/AlertBar'
import Transport from '@/components/scenario/Transport'
import GameOver from '@/components/scenario/GameOver'
import Telemetry from '@/components/scenario/Telemetry'
export default {
  data () {
    return {
      // ======== Games menu fix for Safari =========
      fix: false,
      // ======== Restart Meds =========
      medRestart: false,
      // ===============================
      // ========== GAME OVER ==========
      gameOver: false,
      // ===============================
      transportActive: false,
      medActive: false,
      cardActive: false,
      procedureActive: false,
      respiratoryActive: false,
      physicalActive: false,
      dispatchActive: false,
      VitalsActive: false,
      telemetryActive: false,
      PtHappy: false,
      // ======= Patient Communication Bubble ========
      bubble: 'My chest hurts 10/10',
      // ======= V/S prop data =======
      systolic: 140,
      diastolic: 72,
      map: 91,
      respiratoryRate: 18,
      heartRate: 76,
      oxygenSat: 98,
      bgl: 90,
      ekg: require('../assets/ekg2.png'),
      ekgFull: require('../assets/12-lead.jpg'),
      // ============ Dispatch prop data =========
      callType: 'Cardiac',
      callInfo: 'Male complaining of chest pain.',
      story: '',
      hospitalOne: 'General Hospital (PCI, Stroke, Peds)',
      hospitalTwo: 'University Hospital (PCI, Trauma, EDP)',
      // ============ Physical Assessment props ==============
      complaint: '50 year old male complaining of chest pain for two hours.\n' +
        '         Pain began while he was performing daily maintenance work.\n' +
        '         Patient self administered nitroglycerin tablets with no relief and the pain has become\n' +
        '         progressively worse and is 9/10 now. Patient denies trauma.',
      medicalHistory: 'Diabetes, Hyperlipidemia, Hypertension',
      medications: 'Lipitor, HCTZ, Metformin',
      allergies: 'Seasonal',
      signesSymptoms: 'Middle aged overweight male, with cool, pale and diaphoretic skin complaining of chest pain\n' +
        '              and nausea. No other abnormal findings in physical assessment.',

      // ============ Game Over Props ===============
      fairActions: [],
      wrongActions: [],
      // ============ Procedure Prop Data For Log List===================
      ivAccess: '',
      ioAccess: '',
      needle: '',
      nrb: '',
      cpap: '',
      // procedures: [],
      // ============ Medication log prop ================
      medLog: [],
      // medLogWrong: [],
      // ============ SCORE TRACKING PROP ======================
      score: 0,
      // ============ Restriction Prop ====================
      strike: 0,
      // ============ Transport Prop ===================
      finalEvaluation: '',
      // ================ Get medication Parameters ============
      medicationName: '',
      medicationDose: '',
      medicationUnit: '',
      medicationRoute: '',
      // ================ Get Telemetry Parameters =============
      telemetryMedName: '',
      telemetryMedDose: '',
      telemetryMedUnit: '',
      telemetryMedRoute: '',
      // =============== MEDICATION CONTROL DATA ===============
      asa: [],
      salineCard: [],
      nitroglycerin: [],
      // =============== MEDICATION CONTROL ADMINISTRATION IF IV IO IN PLACE ==============
      ivPlaced: false
    }
  },
  // ================= COMPONENTS ===========================
  components: {
    appScenarioNav: ScenarioNav,
    // appScenarioMenu: ScenarioMenu,
    appScenarioVitalSignes: ScenarioVitalSignes,
    appDispatchInfo: DispatchInfo,
    appPtAssessment: PtAssessment,
    appProcedure: Procedure,
    appCardIntervention: CardIntervention,
    appRespiratory: Respiratory,
    appMedication: Medication,
    appLogList: LogList,
    // appScenarioScore: ScenarioScore,
    appStrikeCounter: StrikeCounter,
    appAlertBar: AlertBar,
    appTransport: Transport,
    appGameOver: GameOver,
    appTelemetry: Telemetry
  },
  mounted () {
    this.respiratoryFluctuation()
    this.heartRateFluctuation()
  },
  watch: {
    strike () {
      if (this.strike >= 3) {
        this.gameOver = true
        this.finalEvaluation = 'Keep practicing you need to become more familiar with your protocols'
      }
    }
  },
  methods: {
    //= =================== Menu Fix for Safari ==============

    menuFix () {
      if (this.dispatchActive === false) {
        this.fix = true
        console.log('fix working' + this.fix)
      } else if (this.dispatchActive === true) {
        this.fix = false
      }
    },
    menuFix2 () {
      if (this.physicalActive === false) {
        this.fix = true
      } else if (this.physicalActive === true) {
        this.fix = false
      }
    },
    menuFix3 () {
      if (this.procedureActive === false) {
        this.fix = true
      } else if (this.procedureActive === true) {
        this.fix = false
      }
    },
    menuFix4 () {
      if (this.cardActive === false) {
        this.fix = true
      } else if (this.cardActive === true) {
        this.fix = false
      }
    },
    menuFix5 () {
      if (this.respiratoryActive === false) {
        this.fix = true
      } else if (this.respiratoryActive === true) {
        this.fix = false
      }
    },
    menuFix6 () {
      if (this.medActive === false) {
        this.fix = true
      } else if (this.medActive === true) {
        this.fix = false
      }
    },
    menuFix7 () {
      if (this.telemetryActive === false) {
        this.fix = true
      } else if (this.telemetryActive === true) {
        this.fix = false
      }
    },
    menuFix8 () {
      if (this.transportActive === false) {
        this.fix = true
      } else if (this.transportActive === true) {
        this.fix = false
      }
    },
    // ================= Transport End Game ================
    transportEndGame () {
      this.gameOver = true
      console.log(this.nitroglycerin.length)
      console.log(this.asa.length)
      if (this.asa.length === 1 && this.nitroglycerin.length >= 2 && this.fairActions.length <= 0 && this.map >= 65) {
        this.finalEvaluation = 'Excellent job you know your protocol'
      } else if (this.asa.length === 1 && this.nitroglycerin.length === 2 && this.fairActions.length === 1 && this.map >= 65) {
        this.finalEvaluation = 'Keep practicing your almost there'
      } else if (this.asa.length === 1 && this.nitroglycerin.length === 2 && this.fairActions.length === 2 && this.map >= 65) {
        this.finalEvaluation = 'You are doing well but you need more practice'
      } else if (this.asa.length === 0 && this.nitroglycerin.length === 2 && this.fairActions.length >= 0 && this.map >= 65) {
        this.finalEvaluation = 'Treatment was not complete!'
        this.fairActions.unshift('Aspirin was not administered')
      } else if (this.asa.length === 0 && this.nitroglycerin.length === 1 && this.fairActions.length >= 0 && this.map >= 65) {
        this.finalEvaluation = 'Treatment was not complete!'
        this.fairActions.push('Pt still had chest pain with adequate blood pressure, one more dose of nitroglycerin was warranted.')
        this.fairActions.unshift('Aspirin was not administered')
        console.log('yes')
      } else if (this.asa.length === 0 && this.nitroglycerin.length > 2 && this.fairActions.length >= 0 && this.map >= 65) {
        this.finalEvaluation = 'Treatment was not complete!'
        this.fairActions.unshift('Aspirin was not administered')
        console.log('yes')
      } else if (this.asa.length === 1 && this.nitroglycerin.length < 2 && this.finalEvaluation >= 0 && this.map >= 65) {
        this.finalEvaluation = 'Treatment was not complete!'
        this.fairActions.unshift('Pt still had chest pain with adequate blood pressure, one more dose of nitroglycerin was warranted.')
      } else if (this.asa.length === 0 && this.nitroglycerin.length === 0 && this.fairActions.length >= 0 && this.map >= 65) {
        this.finalEvaluation = 'No treatment Administered!'
        this.fairActions.unshift('Keep practicing you need to become more familiar with your protocols')
        this.fairActions.unshift('Aspirin and Nitroglycerin were not administered')
      } else if (this.asa.length > 0 && this.nitroglycerin.length === 0 && this.fairActions.length >= 0 && this.map >= 65) {
        this.finalEvaluation = 'Treatment was incomplete!'
        this.fairActions.unshift('Nitroglycerin was not administered!')
        this.fairActions.unshift('Keep practicing you need to become more familiar with your protocols')
      } else if (this.systolic < 100 && this.map < 65) {
        this.finalEvaluation = 'This patient needed IV fluid administration'
        this.fairActions.unshift('Patients blood pressure and MAP are low. You failed to administer IV fluids.')
      }
      // this.finalEvaluation = '1 keep practicing your almost there  2 you are doing good but you need more practice
      // 3 'you need to become more familiar with your protocols
      // no asa 2  nitro
    },
    // ================= RESTART =====================
    restartGame () {
      this.gameOver = false
      this.medRestart = true
      this.strike = 0
      // this.procedures = []
      this.medLog = []
      // this.medLogWrong = []
      this.fairActions = []
      this.wrongActions = []
      this.asa = []
      this.salineCard = []
      this.nitroglycerin = []
      this.bubble = 'My chest hurts 10/10'
      this.systolic = 140
      this.diastolic = 72
      this.map = 91
      this.respiratoryRate = 18
      this.heartRate = 76
      this.oxygenSat = 98
      this.bgl = 90
      this.transportActive = false
      this.medActive = false
      this.cardActive = false
      this.procedureActive = false
      this.respiratoryActive = false
      this.physicalActive = false
      this.dispatchActive = false
      this.VitalsActive = false
      this.telemetryActive = false
      // ======== IV Restart ============
      this.ivPlaced = false
      this.$refs.iv.style.opacity = 0
      // ================================
      this.PtHappy = false
      console.log(this.medRestart)
    },
    // ============== VITAL SIGNS FLUCTUATION FUNCTION =============
    respiratoryFluctuation () {
      setInterval(() => {
        this.respiratoryRate = Math.floor(Math.random() * (20 - 16) + 16)
        // this.respiratoryRate += 15
      }, 13000)
    },
    heartRateFluctuation () {
      setInterval(() => {
        this.heartRate = Math.floor(Math.random() * (77 - 73) + 73)
        // this.respiratoryRate += 15
      }, 5000)
    },
    // ======================================================================
    menuToggle (one, two, three, four, five, six, seven, eight) {
      this.dispatchActive = one
      this.physicalActive = two
      this.procedureActive = three
      this.cardActive = four
      this.medActive = five
      this.respiratoryActive = six
      this.transportActive = seven
      this.telemetryActive = eight
    },
    // ================ Procedure Log Props ============
    faceToggle () {
      this.ivPlaced = true
      this.PtHappy = true
      // this.ivAccess = 'IV access has been established'
      this.medLog.unshift('IV access has been established')
      this.$refs.iv.style.opacity = 1
    },
    io () {
      this.medLog.unshift('IO is to aggressive! Get an IV!')
      // this.ioAccess = 'IO access has been established'
      // this.$refs.io.style.opacity = 1
    },
    needleD () {
      this.medLog.unshift('There is absolutely no need for needle decompression!')
      this.fairActions.unshift('There is absolutely no need for needle decompression!')
      this.strike += 1
      // this.needle = 'Needle decompression performed'
    },
    cpr () {
      this.medLog.unshift('Patient has a pulse!')
      this.fairActions.unshift('Patient Has a pulse!')
      this.strike += 1
    },
    // ================ Respiratory Emits==================
    cannula () {
      this.medLog.unshift('SpO2 is in adequate range, no trouble breathing, no need for supplemental oxygen')
    },
    nonRebreather () {
      this.medLog.unshift('SpO2 is in adequate range, no trouble breathing, no need for supplemental oxygen')
      // this.$refs.nrb.style.opacity = 1
      // this.nrb = 'Non-rebreather with O2 administered'
      // this.$refs.cpap.style.opacity = 0
      // this.$refs.strap1.style.opacity = 0
      // this.$refs.strap2.style.opacity = 0
      // this.$refs.strap3.style.opacity = 0
    },
    bvm () {
      this.medLog.unshift('Absolutely no need to ventilate this patient!')
      this.fairActions.unshift('Absolutely no need to ventilate this patient!')
      this.strike += 1
    },
    cpapPlacement () {
      this.medLog.unshift('There is absolutely no need for CPAP!')
      this.fairActions.unshift('There is absolutely no need for CPAP!')
      this.strike += 1
      // this.$refs.cpap.style.opacity = 1
      // this.$refs.strap1.style.opacity = 1
      // this.$refs.strap2.style.opacity = 1
      // this.$refs.strap3.style.opacity = 1
      // this.cpap = 'CPAP placement successfull'
      // this.$refs.nrb.style.opacity = 0
    },
    intubate () {
      this.medLog.unshift('Absolutely no need to intubate this patient!')
      this.fairActions.unshift('Absolutely no need to intubate this patient!')
      this.strike += 1
    },
    // ================ Cardiac Procedure Emits ================
    defibrillate () {
      this.medLog.unshift('There is no need to defibrillate a patient with a pulse!')
      this.fairActions.unshift('There is no need to defibrillate a patient with a pulse!')
      this.strike += 1
    },
    startPacing () {
      this.medLog.unshift('There is no need to pace this patient!')
      this.fairActions.unshift('There is no need to pace this patient!')
      this.strike += 1
    },
    cardiovertPatient () {
      this.medLog.unshift('There is no need to cardiovert this patient!')
      this.fairActions.unshift('There is no need to cardiovert this patient!')
      this.strike += 1
