      // ========================= Inadequate Telemetry Meds Fro Scenario ==============================
      if (this.telemetryMedName !== 'Aspirin' && this.telemetryMedName !== 'Nitroglycerin Spray' && this.telemetryMedName !== 'ns-kvo' &&
