          <p class="confirm__option-header">
            <span class="confirm__option-span">You are about to start NYC REMAC practice exam.</span>
            Your exam will consist of 120 multiple choice questions. This exam is not timed, so relax and enjoy!
            Hopefully that brief explanation took some anxiety off. Now if your ready then click start and lets
