<script>
import axios from '@/axios-auth'

export default {
  data () {
    return {
      isRendering: false
    }
  },

  mounted () {
    localStorage.isSittingExam = 'no'
    localStorage.questionNumber = 0
    this.checkForPreviousExam()
  },
  methods: {
    checkForPreviousExam () {
      console.log('checking for previous exam')
      axios.get('api/exam/continue-check/', {
        headers: {
          Authorization: 'Token ' + localStorage.token
          // 'Access-Control-Allow-Origin': 'https://noob718.github.io'
        }
        // params: { user_id: this.$store.state.user_id }
      })
        .then(res => {
          const status = res.status
          // const data = res.data
          // console.log('downloaded data = ' + status)
          // eslint-disable-next-line no-constant-condition
          if (status === 200) {
            this.$router.push('/continue-exam')
          } else if (status === 204) {
            console.log('no data found')
            this.isRendering = true
            this.$router.push('/confirm')
          }
        }).catch(err => { console.log(err) })
    }
  }
}
</script>

