<template>
  <div class="procedure__text">
    <p class="center">Medication Administration</p>

    <form @submit.prevent="medData" class="med__form">
<!--      <div class="med__form-instruction">-->
<!--        <p class="med__form-text">Name</p>-->
<!--      </div>-->
      <div class="med__form-col-1">
        <select name="medication" id="medication" v-model="name" ref="name">
          <option value="choose" disabled>Choose medication</option>
          <option value="ns-kvo">Normal Saline KVO</option>
          <option value="ns-250">Normal Saline 250ml Bolus</option>
          <option value="ns-1000">Normal Saline 1000ml Drip</option>
          <option v-for="medication in medications" :key="medication">{{ medication }}</option>
        </select>
      </div>
<!--      <div class="med__form-instruction">-->
<!--        <p class="med__form-text">Dose</p>-->
<!--      </div>-->
      <div class="med__form-col-2" :class="{hide : hideActive}">
        <div class="med__form-input-row">
          <label for="dose">Dose</label>
          <input class="med__form-input" type="number" id="dose" name="dose" min="0" step="0.01" v-model.number="dose" ref="dose" @blur="valid">
        </div>
        <div class="med__form-input-row" :class="{hide : hideActive}">
          <label class="unit-label" for="unit">Unit</label>
          <select name="unit" id="unit" v-model="unit">
            <option value="mg">mg</option>
            <option value="mcg">mcg</option>
            <option value="gm">gm</option>
            <option value="ml">ml</option>
            <option value="meq">mEq</option>
            <option value="one-unit">1 unit</option>
          </select>
        </div>
      </div>
<!--      <div class="med__form-instruction">-->
<!--        <p class="med__form-text">Route</p>-->
<!--      </div>-->
      <div class="med__form-col-3" :class="{hide : hideActive}">
        <label class="route-label" for="route">Route</label>
        <select name="route" id="route" v-model="route">
          <option value="iv-bolus">IV Bolus</option>
          <option value="iv-drip">IV Drip</option>
          <option value="io">IO</option>
          <option value="in">IN</option>
          <option value="im">IM</option>
          <option value="po">PO</option>
          <option value="sl">SL</option>
        </select>
      </div>
      <button class="procedure__text-style">Administer</button>
    </form>
  </div>
</template>

<script>
export default {
  props: ['medRestart'],
  emits: ['med-name'],
  data () {
    return {
      medications: [
        'Aspirin', 'Nitroglycerin Spray', 'Adenosine', 'Amiodarone',
        'Cardizem', 'magnesium Sulfate', 'Glucose Paste', 'Glucagon',
        'D50', 'D25', 'D10', 'Ondansetron', 'Solumedrol', 'Dexamethasone',
        'Benadryl', 'Epinephrine 1:1000', 'Epinephrine 1:10,000', 'Epinephrine 1:100,000', 'Lidocaine',
        'Albuterol', 'Ipratropium Bromide', 'Sodium Bicarb', 'Calcium Chloride',
        'Midazolam', 'Diazepam', 'Lorazepam', 'Ketamine', 'Hydroxocobalamin',
        'Sodium Thiosulfate'
      ],
      name: 'choose',
      dose: null,
      unit: 'mg',
      route: 'iv-drip',
      // ======== INPUT VALIDATION ===========
      // validationActive: false
      // ============ Hide the dose ===========
      hideActive: false
    }
  },
  watch: {
    medRestart () {
      if (this.medRestart === true) {
        this.name = 'choose'
        this.dose = null
        this.unit = 'mg'
        this.route = 'iv-drip'
      }
    },
    name () {
      if (this.name === 'ns-kvo' || this.name === 'ns-250' || this.name === 'ns-1000') {
        this.hideActive = true
        this.dose = 1000
      } else if (this.name !== 'ns-kvo' || this.name !== 'ns-250' || this.name !== 'ns-1000') {
        this.hideActive = false
        this.dose = null
      }
      if (this.name !== 'choose') {
        this.$refs.name.style.borderColor = 'transparent'
      }
    }
  },
  methods: {
    medData () {
      if (this.dose === null) {
        this.$refs.dose.style.borderColor = '#d32732'
      } else if (this.dose <= 0) {
        this.$refs.dose.style.borderColor = '#d32732'
      } else if (this.name === 'choose') {
        this.$refs.name.style.borderColor = '#d32732'
      } else {
        this.$emit('med-name', this.name, this.dose, this.unit, this.route)
        this.dose = null
        this.name = 'choose'
        this.unit = 'mg'
        this.route = 'iv-drip'
      }
    },
    valid () {
      if (this.dose !== null) {
        this.$refs.dose.style.borderColor = 'transparent'
      }
    }
  }
}
</script>

