<template>
  <div class="feedback__wrapper">
    <div class="feedback">
      <p class="feedback__header">Your feedback is very valuable</p>
      <form @submit.prevent="submitFeedbackForm" class="feedback__form">
        <div class="feedback__form-row">
          <label for="option" class="feedback__form-label">Feedback type</label>
          <select class="feedback__form-input" name="option" id="option" v-model="feedbackOption">
            <option value="account">Account Error</option>
            <option value="billing">Billing Error</option>
            <option value="exam">Exam Error</option>
            <option value="other">Not listed - Other</option>
          </select>
        </div>
        <div class="feedback__form-row">
          <label v-if="descriptionPresent" for="textarea" class="feedback__form-label">Provide description</label>
          <label v-if="!descriptionPresent" for="textarea" class="feedback__form-label">
            Provide description
            <span class="required">
              * required
            </span>
          </label>
          <textarea class="feedback__form-input" name="textarea" id="textarea" cols="40" rows="10" v-model.trim="feedbackDescription"></textarea>
        </div>
        <button class="feedback__form-button">Submit</button>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      feedbackOption: 'account',
      feedbackDescription: '',
      descriptionPresent: true
    }
  },
  watch: {
    feedbackDescription () {
      if (this.feedbackDescription !== '') {
        this.descriptionPresent = true
      }
    }
  },
  methods: {
    submitFeedbackForm () {
      if (this.feedbackDescription === '') {
        this.descriptionPresent = false
      } else {
        this.feedbackOption = 'account'
        this.feedbackDescription = ''
      }
    }
  }
}
</script>
