<template>
<div class="strike" ref="counter">
  <div class="strike__description">
    <p class="strike__description-text">Restrictions</p>
  </div>
  <div class="strike__restriction">
    <p class="strike__restriction-counter">{{ strike }}</p>
  </div>
</div>
</template>

<script>
export default {
  props: ['strike'],
  watch: {
    strike () {
      if (this.strike === 1) {
        this.$refs.counter.style.backgroundColor = '#71B280'
      } else if (this.strike === 2) {
        this.$refs.counter.style.backgroundColor = '#f26c4f'
      } else if (this.strike === 0) {
        this.$refs.counter.style.backgroundColor = 'transparent'
      }
    }
  }
}
</script>

