<template>
<div>
  <main class="exam__main" id="color">

    <div v-show="flagBoxToggle" class="flag__wrapper">
    <div class="flag">
      <button @click="displayFlagForm" class="flag__close"></button>
      <form @submit.prevent="submitFlagForm" class="flag__form">
        <div class="flag__form-row">
          <label for="option" class="flag__form-label">Select error type</label>
          <select class="flag__form-input" name="option" id="option" v-model="flagOption">
            <option value="spelling">Spelling Error</option>
            <option value="protocol">Protocol Discrepancy</option>
            <option value="dosing">Dosing Error</option>
            <option value="treatment">Treatment Error</option>
            <option value="calculation">Calculation Error</option>
            <option value="other">Other</option>
          </select>
        </div>
        <div class="flag__form-row">
          <label v-if="descriptionPresent" for="textarea" class="flag__form-label">Provide description</label>
          <label v-if="!descriptionPresent" for="textarea" class="flag__form-label">
            Provide description
            <span class="required">
              * required
            </span>
          </label>
          <textarea class="flag__form-input" name="textarea" id="textarea" cols="30" rows="10" v-model.trim="flagDescription"></textarea>
        </div>
        <button class="flag__form-button">Submit</button>
      </form>
    </div>
    </div>

    <section class="questions">

      <div class="questions__title">
        <h1 class="questions__title-text">NYC REMAC Practice Exam</h1>
        <div class="questions__question-number">
          <h2 class="questions__question-number-text">{{ questionDisplayNumber }}/{{ questions.length }}</h2>
        </div>
        <button @click="displayFlagForm" class="questions__flag">
          <img src="../../assets/flag.png" alt="" class="questions__flag-img">
        </button>
      </div>

      <div class="questions__question">
        <img src="" alt="" class="questions__question-text--image">
        <h3 class="questions__question-text">{{questionText}}</h3>
      </div>

    </section>
    <!------------------------------------------------------------------------------------------------->
    <section class="answers" v-for="(question, index) in questions.slice(questionNumber,questionDisplayNumber)" :key="index">
      <div class="answers__box">
            <div class="answers__form">
<!--              <div v-on:click="activateRow(true, false, false, false); saveChosenAnswer(question.choice_one)"-->
              <div v-on:click="activateRowOnPress('choice1'); saveChosenAnswer(question, question.choice_one)"
                   ref="row"
                   :class="{'answers__form-toggle' : checked}"
                   class="answers__form-row"
                    id="row">

                <p ref="textWidth1" class="answers__text answers__label-text-1">{{ question.choice_one }}</p>

              </div>

<!--              <div v-on:click="activateRow(false,true,false, false); saveChosenAnswer(question.choice_two)"-->
              <div v-on:click="activateRowOnPress('choice2'); saveChosenAnswer(question, question.choice_two)"
                   ref="rowWidth1"
                   :class="{'answers__form-toggle' : checked2}"
                   class="answers__form-row row-check-2">

                <p ref="textWidth2" class="answers__text answers__label-text-2">{{question.choice_two}}</p>

              </div>
<!--              <div v-on:click="activateRow(false,false,true,false); ; saveChosenAnswer(question.choice_three)"-->
              <div v-on:click="activateRowOnPress('choice3'); saveChosenAnswer(question, question.choice_three)"
                   ref="rowWidth1"
                   :class="{'answers__form-toggle' : checked3}"
                   class="answers__form-row row-check-3">

                <p ref="textWidth3" class="answers__text answers__label-text-3">{{ question.choice_three }}</p>

              </div>

<!--              <div v-on:click="activateRow(false,false,false,true); saveChosenAnswer(question.choice_four)"-->
              <div v-on:click="activateRowOnPress('choice4'); saveChosenAnswer(question, question.choice_four)"

                   ref="rowWidth1"
                   :class="{'answers__form-toggle' : checked4}"
                   class="answers__form-row row-check-4">

                <p ref="textWidth4" class="answers__text answers__label-text-4">{{ question.choice_four}}</p>

              </div>
            </div>

          <div class="answers__buttons">
            <div class="answers__buttons-previous">
              <button v-if="questionNumber > 0" class="answers__btn" @click="previousButtonPressed">Back</button>
            </div>
            <div class="answers__buttons-nex">
              <button class="answers__btn" @click="nextButtonPressed">{{ nextButtonTitle }}</button>
            </div>
          </div>
      </div>

    </section>

  </main>
</div>
</template>

<script>
import axios from '@/axios-auth'
// eslint-disable-next-line no-unused-vars
import router from '@/router'
export default {
  data () {
    return {
      // ========= FLAG FORM =========
      flagBoxToggle: false,
      flagOption: 'spelling',
      flagDescription: '',
      descriptionPresent: true,
      // =============================
      isRendering: false,
      checked: false,
      checked2: false,
      checked3: false,
      checked4: false,
      rowWidth: 0,
      textWidth: 0,
      percent: 0,
      sessionID: null,
      examID: null,
      moreQuestionsAvailableNext: true,
      nextButtonTitle: 'Next',
      previousButtonTitle: 'Previous',
      questionAnswer: {
        questionText: null,
        questionId: null,
        answerId: null,
        userAnswerChoice: null
      },
      // Exam Variables

      questionText: '',
      questionNumber: 0,
      questionDisplayNumber: 1,
      placeInExamReached: 0,
      numberOfPossibleChoices: null,
      userAnswer: [],
      userAnswerArray: [],
      questions: [],
      currentQuestion: [],
      answers: [],
      correctAnswersArray: [],
      num_of_exam_questions: 20,
      num_of_scenarios: 2,
      percentOfMedicalQuestions: 50,
      percentOfTraumaQuestions: 0,
      percentOfHazmatQuestions: 0,
      percentOfArrestQuestions: 0,
      percentOfGopQuestions: 0,
      percentOfAppendixQuestions: 50,
      lastExamId: 119,
      // Todo: This is the boolean for checking for multiple answers
      multipleChoicesAllowed: false
    }
  },
  mounted () {
    // When page is refreshed during active exam it calls the page number
    // this.getQuestionsFromDatabase()
    if (localStorage.isSittingExam === 'yes') {
      console.log('is sitting exam')
      this.$router.push('/continue-exam')
    } else {
      this.checkForUncompletedExam()
    }
  },
  // ======= MULTIPLE CHOICE FIX ============
  watch: {
    multipleChoicesAllowed () {
      this.activateRowOnPress()
    },
    flagDescription () {
      if (this.flagDescription !== '') {
        this.descriptionPresent = true
      }
    }
  },
  methods: {
    // ============== DISPLAY FORM FOR FLAGGED QUESTIONS ==============
    displayFlagForm () {
      this.flagBoxToggle = !this.flagBoxToggle
    },
    submitFlagForm () {
      if (this.flagDescription === '') {
        this.descriptionPresent = false
      } else {
        console.log(this.flagOption + this.flagDescription)
        console.log(this.questionText)
        console.log(this.questionNumber)
        console.log(this.examID)
        this.flagOption = 'spelling'
        this.flagDescription = ''
      }
    },
    // ==================================================================
    checkForPreviousExam () {
      console.log('checkForPreviousExam method holder')
      this.$store.dispatch('checkForPreviousExam', this.sessionID)
    },
    activateRow (val1, val2, val3, val4) {
      this.checked = val1
      this.checked2 = val2
      this.checked3 = val3
      this.checked4 = val4
    },
    activateRowOnPress (val) {
      if (this.multipleChoicesAllowed) {
        switch (val) {
          case 'choice1':
            this.checked = !this.checked
            break
          case 'choice2':
            this.checked2 = !this.checked2
            break
          case 'choice3':
            this.checked3 = !this.checked3
            break
          case 'choice4':
            this.checked4 = !this.checked4
        }
      } else {
        switch (val) {
          case 'choice1':
            this.activateRow(true, false, false, false)
            break
          case 'choice2':
            this.activateRow(false, true, false, false)
            break
          case 'choice3':
            this.activateRow(false, false, true, false)
            break
          case 'choice4':
            this.activateRow(false, false, false, true)
        }
      }
    },
    resetChecks () {
      this.checked = false
      this.checked2 = false
      this.checked3 = false
      this.checked4 = false
    },
    removeAnswerFromArray: function (userChoice) {
      const index = this.userAnswer.indexOf(userChoice)
      if (index > -1) {
        this.userAnswer.splice(index, 1)
      }
      console.log(this.userAnswer)
    },
    // Exam Functions
    saveChosenAnswer (question, userChoice) {
      console.log('answer saved activated')
      // TODO: Need to made a function that grabs all multiple selected radio buttons
      if (!this.multipleChoicesAllowed) {
        this.userAnswer = []
        this.userAnswer.push(userChoice)
      } else {
        this.userAnswer = []
        if (this.checked) {
          this.userAnswer.push(question.choice_one)
        }
        if (this.checked2) {
          this.userAnswer.push(question.choice_two)
        }
        if (this.checked3) {
          this.userAnswer.push(question.choice_three)
        }
        if (this.checked4) {
          this.userAnswer.push(question.choice_four)
        }
      }
      // console.log(this.userAnswer)
      // this.saveUserAnswerToDatabase(this.userAnswer, this.examID, this.sessionID, this.questionNumber, this.questions[this.questionNumber].question_id)
    },
    saveAnswer () {
      this.saveUserAnswerToDatabase(this.userAnswer, this.examID, this.sessionID, this.questionNumber, this.questions[this.questionNumber].question_id)
    },
    nextButtonPressed () {
      if (this.moreQuestionsAvailableNext === false) {
        // routing is done in the dispatch function
        this.$store.dispatch('setExamToCompleted', this.sessionID)
      } else {
        this.saveUserAnswerToDatabase(this.userAnswer, this.examID, this.sessionID, this.questionNumber, this.questions[this.questionNumber].question_id)
        this.userAnswerArray[this.questionNumber] = this.userAnswer
        this.questionNumber++
        this.questionDisplayNumber++
        this.questionText = this.questions[this.questionNumber].question_text
        this.checkIfAnswerExistsAndSaveToVariable(this.examID, this.sessionID, this.questions[this.questionNumber].question_id, this.questionNumber)
        if (this.questionDisplayNumber === this.questions.length) {
          this.nextButtonTitle = 'Score Test'
          this.moreQuestionsAvailableNext = false
        }
        // var upcomingQuestions = this.questions[this.questionNumber].correct_choice
        // console.log('number of choices = ' + upcomingQuestions)
        // console.log('multiple choice = ' + this.multipleChoicesAllowed)
        // ===============================================
        // ===========MULTIPLE CHOICE FIX================
        this.multipleChoicesAllowed = this.questions[this.questionNumber].multipleAnswers
        console.log(this.questions[this.questionNumber].multipleAnswers)
        console.log(this.multipleChoicesAllowed)
        // ===============================================
        this.resetChecks()
        this.checkIfLastQuestion()
      }
    },
    previousButtonPressed () {
      if (this.questionNumber >= 0) {
        this.userAnswerArray[this.questionNumber] = this.userAnswer
        this.questionNumber--
        this.questionDisplayNumber--
        this.questionText = this.questions[this.questionNumber].question_text
        this.multipleChoicesAllowed = this.questions[this.questionNumber].multipleAnswers
      } else {
        this.userAnswerArray[this.questionNumber] = this.userAnswer
        this.questionText = this.questions[this.questionNumber].question_text
        this.multipleChoicesAllowed = this.questions[this.questionNumber].multipleAnswers
        // TODO: display value goes below zero - add if loop to catch
        this.questionDisplayNumber--
      }
      this.checkIfAnswerExistsAndSaveToVariable(this.examID, this.sessionID, this.questions[this.questionNumber].question_id, this.questionNumber)
      const currentQuestion = this.questions[this.questionNumber]
      for (const answer in this.userAnswer) {
        if (answer === currentQuestion.choice_one) {
          this.checked = true
        }
        if (answer === currentQuestion.choice_two) {
          this.checked = true
        }
        if (answer === currentQuestion.choice_three) {
          this.checked = true
        }
        if (answer === currentQuestion.choice_four) {
          this.checked = true
        }
      }
    },
    checkIfLastQuestion () {
      if (this.moreQuestionsAvailableNext === true) {
        this.nextButtonTitle = 'Next'
      } else {
        this.moreQuestionsAvailableNext = false
        this.nextButtonTitle = 'Score Test'
      }
    },
    // Network methods
    getQuestionsFromDatabase () {
      console.log('number of medical question = ' + this.percentOfMedicalQuestions, this.percentOfMedicalQuestions)
      axios.post('api/exam/create_new_exam/', {
        num_of_exam_questions: this.num_of_exam_questions,
        num_of_scenarios: this.num_of_scenarios,
        percent_of_medical_questions: this.percentOfMedicalQuestions,
        percent_of_trauma_questions: this.percentOfTraumaQuestions,
        percent_of_hazmat_questions: this.percentOfHazmatQuestions,
        percent_of_arrest_questions: this.percentOfArrestQuestions,
        percent_of_gop_questions: this.percentOfGopQuestions,
        percent_of_appendix_questions: this.percentOfAppendixQuestions,
        last_exam_id: this.lastExamId
      }, {
        headers: { Authorization: 'Token ' + localStorage.token }
      }
      )
        .then(res => {
          console.log(res)
          const data = res.data
          // const examQuestions = data.session_exam.questions
          // const examQuestions = data.sorted_questions]
          const sortedQuestions = data.sorted_questions
          this.examID = data.session_exam.id
          this.sessionID = data.id
          this.questionNumber = data.last_question_completed
          this.questionText = sortedQuestions[this.questionNumber].question_text
          for (const question in sortedQuestions) {
            this.questions.push({
              question_text: sortedQuestions[question].question_text,
              choice_one: sortedQuestions[question].choice_one,
              choice_two: sortedQuestions[question].choice_two,
              choice_three: sortedQuestions[question].choice_three,
              choice_four: sortedQuestions[question].choice_four,
              slug: sortedQuestions[question].slug,
              user_choice: new Array(sortedQuestions[question].number_of_correct_choices),
              question_id: sortedQuestions[question].id,
              use_index: sortedQuestions[question].order_in_sequence_or_scenario,
              multipleAnswers: sortedQuestions[question].has_multiple_answers
            })
            this.multipleChoicesAllowed = this.questions[this.questionNumber].multipleAnswers
            this.questions.sort((a, b) => a.order_in_sequence_or_scenario - b.order_in_sequence_or_scenario)

            // TODO: I dont know if this is actually needed. lines 423 to 429
            // axios.get('api/exam/user-answers/', {
            //   headers: { Authorization: 'Token ' + localStorage.token },
            //   params: { session_id: data.id }
            // })
            //   .then(answerRes => {
            //     console.log(answerRes)
            //   })
          }
          this.isRendering = true
        })
        .catch(err => { console.log(err) })
    },
    saveUserAnswerToDatabase (userAnswer, examID, sessionID, counter, questionID) {
      axios.post('api/exam/save-answer/',
        {
          current_exam: examID,
          incoming_choice: userAnswer,
          counter: counter,
          session_id: sessionID,
          question_id: questionID
        }, {
          headers: { Authorization: 'Token ' + localStorage.token }
        }
      )
        .then(res => {
          console.log('data sent', res)
        })
        .catch(err => { console.log(err) })
    },
    checkIfAnswerExistsAndSaveToVariable (examID, sessionID, questionID, questionNumber) {
      // check the question to see if it was already answered and return the question information and previous choice.
      // returns nothing if no answer was previously selected
      axios.post('api/exam/check-answer/',
        {
          current_exam: examID,
          session_id: sessionID,
          question_id: questionID,
          question_number: questionNumber
        }, {
          headers: { Authorization: 'Token ' + localStorage.token }
        }
      )
        .then(res => {
          console.log('data returned', res)
          this.questionAnswer.answerId = res.data.id
          this.questionAnswer.questionId = res.data.question
          this.questionAnswer.questionText = res.data.question_text
          this.questionAnswer.userAnswerChoice = res.data.choice_text
          this.userAnswer = res.data.choice_text
          this.displayQuestionChoiceLabel(this.userAnswer)
          console.log('user choice returned', res.data.choice_text)
        })
        .catch(err => { console.log(err) })
    },
    displayQuestionChoiceLabel (choice) {
      const question = this.questions[this.questionNumber]
      switch (choice) {
        case question.choice_one:
          this.activateRow(true, false, false, false)
          break
        case question.choice_two:
          this.activateRow(false, true, false, false)
          break
        case question.choice_three:
          this.activateRow(false, false, true, false)
          break
        case question.choice_four:
          this.activateRow(false, false, false, true)
          break
      }
    },
    checkForUncompletedExam () {
      console.log('login token ' + localStorage.token)
      axios.post('api/exam/start-exam/', {
        num_of_exam_questions: this.num_of_exam_questions,
        num_of_scenarios: this.num_of_scenarios,
        percent_of_medical_questions: this.percentOfMedicalQuestions,
        percent_of_trauma_questions: this.percentOfTraumaQuestions,
        percent_of_hazmat_questions: this.percentOfHazmatQuestions,
        percent_of_arrest_questions: this.percentOfArrestQuestions,
        percent_of_gop_questions: this.percentOfGopQuestions,
        percent_of_appendix_questions: this.percentOfAppendixQuestions,
        last_exam_id: this.lastExamId
      }, {
        headers: {
          // 'Access-Control-Allow-Origin': 'http://localhost:8080',
          Authorization: 'Token ' + localStorage.token
        }
      }
      )
        .then(res => {
          console.log(res)
          const data = res.data
          const sortedQuestions = data.sorted_questions
          this.examID = data.session_exam.id
          this.sessionID = data.id
          for (const question in sortedQuestions) {
            this.questions.push({
              question_text: sortedQuestions[question].question_text,
              choice_one: sortedQuestions[question].choice_one,
              choice_two: sortedQuestions[question].choice_two,
              choice_three: sortedQuestions[question].choice_three,
              choice_four: sortedQuestions[question].choice_four,
              slug: sortedQuestions[question].slug,
              user_choice: new Array(sortedQuestions[question].number_of_correct_choices),
              question_id: sortedQuestions[question].id,
              use_index: sortedQuestions[question].order_in_sequence_or_scenario,
              multipleAnswers: sortedQuestions[question].has_multiple_answers
            })
            this.multipleChoicesAllowed = this.questions[this.questionNumber].multipleAnswers
            this.questions.sort((a, b) => a.order_in_sequence_or_scenario - b.order_in_sequence_or_scenario)

            // TODO: I dont know if this is actually needed. lines 423 to 429
            // axios.get('api/exam/user-answers/', {
            //   headers: { Authorization: 'Token ' + localStorage.token },
            //   params: { session_id: data.id }
            // })
            //   .then(answerRes => {
            //     console.log(answerRes)
            //   })
          }
          this.questionNumber = data.last_question_completed
          this.questionDisplayNumber = data.last_question_completed + 1
          // if (localStorage.isSittingExam !== 'yes') {
          //   console.log('in false loop')
          //   this.questionNumber = data.last_question_completed
          //   this.questionDisplayNumber = data.last_question_completed + 1
          // } else {
          //   console.log('in positive loop')
          //   if (data.last_question_viewed < this.questions.length) {
          //     this.questionNumber = data.last_question_viewed + 1
          //     this.questionDisplayNumber = data.last_question_viewed + 2
          //   } else {
          //     this.questionNumber = data.last_question_viewed
          //     this.questionDisplayNumber = data.last_question_viewed + 1
          //   }
          // }

          this.questionText = sortedQuestions[this.questionNumber].question_text
          this.isRendering = true
          localStorage.isSittingExam = 'yes'
        })
        .catch(err => { console.log(err) })
    }
  }
}
</script>

<style lang="scss" scoped>
