      font-weight: 600;
      letter-spacing: .1rem;
      cursor: pointer;
      transition: all .2s;
      &:hover {
        border: .2rem solid #ff5353;
        background-color: #ff5353;
        color: #ededed;
      }
      &:focus {
        background-color: #ff5353;
      }
    }
  }
}

