.animation-heart {
  animation: heart 1s infinite;
  animation-iteration-count: 5;
}
@keyframes heart {
  0% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.animation-star {
  animation: star 3s;
  animation-iteration-count: 5;
}
@keyframes star {
  0% {
    transform: rotate(0);
  }
  25% {
    transform: scale(1.1) rotate(45deg);
  }
  50% {
    transform: rotate(-25deg);
  }
  100% {
    transform: rotate(0);
  }
}
.animation{
  animation: arrow 10s;
  animation-iteration-count: 3;
}
@keyframes arrow {
  0% {
    transform-origin: center;
    transform: rotate(-360deg);
  }
  100% {
    transform: rotate(0);
  }
}
