<template>
  <div class="timer" ref="timer" :class="{'border' : borderFlash}">
    <p class="clock">{{displaySeconds}}</p>
  </div>
</template>

<script>
export default {
  data () {
    return {
      i: null,
      displaySeconds: 15,
      borderFlash: false
    }
  },
  mounted () {
    // this.updateTimeFunc()
    this.secondsFunc()
    // this.minutesFunc()
    // this.secondsStop()
  },
  watch: {
    displaySeconds (value) {
      if (value === 10) {
        this.borderFlash = true
      } else if (value === 0) {
        clearInterval(this.i)
        this.borderFlash = false
      }
    }
  },
  methods: {
    secondsFunc () {
      this.i = setInterval(() => {
        this.displaySeconds--
      }, 1000)
    }
  }
}
</script>

<style lang="scss" scoped>
.timer {
    font-size: 3rem;
    font-weight: 800;
    padding: 1rem 1rem;
    border-radius: 1.5rem;
    color: #232323;
    border: .4rem solid rgba(0, 189, 214, 0.2);
    //background-color: rgba(225, 0, 169,.3);

    @media only screen and (max-width: 23.4375em){
      font-size: 2.5rem;
    }
}
.border {
  animation: flash 1s infinite;
}
@keyframes flash {
  0% {
    border: .4rem solid transparent;
  }
  100% {
    border: .4rem solid #ff5353;
  }
}
</style>
