<script>
import axios from '@/axios-auth'

export default {
  data () {
  },
  mounted () {
    localStorage.questionNumber = 0
    localStorage.isSittingExam = 'no'
  },
  methods: {
    deleteIncompleteRemacExamsAndStartNewExam () {
      console.log(localStorage.token)
      axios.post('api/account/delete-session/', {
        examCategory: 1
      }, {
        headers: {
          Authorization: 'Token ' + localStorage.token
        }
      })
        .then(res => {
          console.log('session deleted')
          this.$router.push('/confirm')
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
