            <label for="password-new" class="form__label">New Password</label>
          </div>

          <div class="form__group">
            <input type="password" class="form__input-pass" placeholder="Confirm Password" id="password-confirm" required v-model="passwordConfirm">
            <label for="password-confirm" class="form__label-pass">Password</label>
          </div>
          <p style="color: crimson">{{passwordErrorMessage}}</p>
          <button type="submit" class="login__btn">Submit</button>
