<script>
import axios from '@/axios-auth'
import router from '../router/index'
export default {

  data () {
    return {
      passwordErrorMessage: '',
      token: null,
      passwordNew: '',
      passwordConfirm: '',
      labelActive: true,
      opacityNone: {
        opacity: 0
      }
    }
  },
  mounted () {
    this.token = this.$route.query.token
  },
  methods: {
    printed () {
      console.log('this printed')
    },
    onSubmit () {
      // TODO: Again.. email validation
      axios.post('/api/account/password_reset/confirm/', {
        token: this.token,
        password: this.passwordNew
      })
        .then(res => {
          console.log(res)
          if (res.status === 200) {
            router.push('/confirm-reset')
          } else {
            this.passwordErrorMessage = 'something wrong'
          }
        })
    }
  }

}
