</template>

<script>
import axios from '@/axios-auth'

export default {
  data () {
    return {
      firstName: null,
      lastName: null,
      userName: null,
      password: null,
      email: null,
      errorMessages: [],
      /// ///////////////
      loading: false,
      stripe: window.Stripe('pk_test_51HJs0WB3vj7bEMY1ry7qXDcfZa62v4UiEUihCfWKeip9DwUKHaeUUC58Hhk5PWjw7fLZk7XjwIZHGpmQCdjnO4Eq005kgmzSf5'),
      elements: null,
      publishableKey: 'pk_test_51HJs0WB3vj7bEMY1ry7qXDcfZa62v4UiEUihCfWKeip9DwUKHaeUUC58Hhk5PWjw7fLZk7XjwIZHGpmQCdjnO4Eq005kgmzSf5',
      stripeClientSecretId: null,
      items: [
        {
          name: '5 Day Plan',
          product_id: 'prod_IQDwGkwxOAEm3D',
          priceId: 'price_1HpNUXB3vj7bEMY1fvXxlxBJ',
          quantity: 1,
          days: 5
        },
        {
          name: '30 Day Plan',
          product_id: 'prod_ITGKyQM7VDkm2G',
          priceId: 'price_1HsJnaB3vj7bEMY1NFum7Mll',
          quantity: 1,
          days: 30
        },
        {
          name: 'Annual Plan',
          product_id: 'prod_ITGKanJbs02jVP',
          priceId: 'price_1HsJoBB3vj7bEMY1P721X2He',
          quantity: 1,
          days: 366
        }
      ],
      chosenProductTitle: 'test',
      chosenProductObject: null,
      accessPlan: null,
      successUrl: 'your-success-url',
      cancelUrl: 'your-cancel-url',
      card: null,
      plan1: '5 Day Access',
      plan2: '30 Day Access',
      plan3: 'Annual Access',
      selected: ''
    }
  },
  mounted () {
    this.elements = this.stripe.elements()
    this.chosenProductObject = this.items[0]
    this.chosenProductTitle = this.chosenProductObject.name

    this.fetchPaymentIntent()
    this.card = this.elements.create('card')
    // Stripe injects an iframe into the DOM
    this.card.mount('#card-element')
    this.cardError = this.card.error_detail
  },
  methods: {
    fiveDayPlanChosen () {
      this.chosenProductObject = this.items[0]
      this.chosenProductTitle = this.chosenProductObject.name
    },
    tenDayPlanChosen () {
      this.chosenProductObject = this.items[1]
      this.chosenProductTitle = this.chosenProductObject.name
    },
    annualPlanChosen () {
      this.chosenProductObject = this.items[2]
      this.chosenProductTitle = this.chosenProductObject.name
    },
    fetchPaymentIntent () {
      axios.post('payments/create-payment-intent/', {
        product: this.chosenProductObject
      })
        .then(res => {
          const data = res.data
          this.stripeClientSecretId = data.clientSecret
        })
    },
    fetchPaymentIntentAndSubmitPayment () {
      axios.post('payments/create-payment-intent/', {
        product: this.chosenProductObject
      })
        .then(res => {
          const data = res.data
          this.stripeClientSecretId = data.clientSecret
          console.log(this.chosenProductObject)
          this.submitPayment()
        })
    },
    submitPayment () {
      this.fetchPaymentIntent()
      this.stripe.confirmCardPayment(this.stripeClientSecretId, {
        payment_method: {
          card: this.card,
          billing_details: {
            name: this.firstName + ' ' + this.lastName,
            email: this.email
          }
        }
      }).then(result => {
        if (result.error) {
          console.log(result.error.message)
          this.$store.dispatch('loginToBillingError', { username: this.userName, password: this.password })
        } else {
          if (result.paymentIntent.status === 'succeeded') {
            this.addDaysToRegisteredUserAndLogin()
            console.log('succeeded')
          }
        }
      })
    },
    registerUser () {
      this.errorMessages = []
      console.log('data being sent ' + this.firstName)
      axios.post('api/account/register', {
        first_name: this.firstName,
        last_name: this.lastName,
        username: this.userName,
        password: this.password,
        email: this.email
      })
        .then(res => {
          const data = res.data
          if (data.response !== 'successfully registered a new user') {
            if (typeof data.first_name !== 'undefined') {
              this.errorMessages.push('First Name: ' + data.first_name[0])
            }
            if (typeof data.last_name !== 'undefined') {
              this.errorMessages.push('Last Name: ' + data.last_name[0])
            }
            if (typeof data.username !== 'undefined') {
              this.errorMessages.push('Username: ' + data.username[0])
            }
            if (typeof data.password !== 'undefined') {
              this.errorMessages.push('password: ' + data.password[0])
            }
            if (typeof data.email !== 'undefined') {
              this.errorMessages.push('email: ' + data.email[0])
            }
          } else {
            console.log(data)
            localStorage.token = data.token
            console.log(localStorage.token)
            this.fetchPaymentIntentAndSubmitPayment()
            // this.$store.dispatch('login', { username: this.userName, password: this.password })
          }
        })
        .catch(err => console.log('error registration attempt', err))
    },
    addDaysToRegisteredUserAndLogin () {
      axios.post('payments/add-plan-to-account/', {
        days: this.chosenProductObject.days
      }, {
        headers: {
          Authorization: 'Token ' + localStorage.token
        }
      })
        .then(res => {
          this.$store.dispatch('login', { username: this.userName, password: this.password })
        })
        .catch(e => {
          // TODO:- IMPLEMENT CONSOLE LOG ON FRONT END PAGE
          console.log('There was a problem granting access to your account. Please contact Customer Support for further assistance.')
        })
    }
  }
}
</script>

