<template>
  <div class="telemetry">
    <p class="center">Telemetry</p>
    <div class="telemetry__doc"></div>
    <form @submit.prevent="telemetryData" class="telemetry__form">
      <div class="telemetry__form-col-1">
        <select class="telemetry__select" name="telemetry-medication" id="telemetry-medication" v-model="telMedName" ref="name">
          <option value="ask" disabled>Ask for orders</option>
          <option value="ns-kvo">Normal Saline KVO</option>
          <option value="ns-250">Normal Saline 250ml Bolus</option>
          <option value="ns-1000">Normal Saline 1000ml Drip</option>
          <option v-for="telMedication in telMedications" :key="telMedication">{{ telMedication }}</option>
        </select>
      </div>

      <div class="telemetry__form-col-2" :class="{hide : hide}">
        <div class="telemetry__form-row" :class="{hide : hide}">
          <label class="telemetry__label" for="dose">Dose</label>
          <input class="telemetry__input" type="number" id="dose" name="dose" min="0" step="0.01" v-model.number="telMedDose" ref="dose" @blur="valid">
        </div>
        <div class="telemetry__form-row" :class="{hide : hide}">
          <label class="telemetry__label telemetry__unit" for="unit">Unit</label>
          <select class="telemetry__select" name="unit" id="unit" v-model="telMedUnit">
            <option value="mg">mg</option>
            <option value="mcg">mcg</option>
            <option value="gm">gm</option>
            <option value="ml">ml</option>
            <option value="meq">mEq</option>
            <option value="one-unit">1 unit</option>
          </select>
        </div>
      </div>
      <div class="telemetry__form-col-3" :class="{hide : hide}">
        <label class="telemetry__label telemetry__route" for="route">Route</label>
        <select class="telemetry__select" name="route" id="route" v-model="telMedRoute">
          <option value="iv-bolus">IV Bolus</option>
          <option value="iv-drip">IV Drip</option>
          <option value="io">IO</option>
          <option value="in">IN</option>
          <option value="im">IM</option>
          <option value="po">PO</option>
          <option value="sl">SL</option>
        </select>
      </div>
      <button class="telemetry__button">Administer</button>
    </form>
  </div>
</template>

<script>
export default {
  props: ['medRestart'],
  emits: ['tel-data'],
  data () {
    return {
      telMedications: [
        'Aspirin', 'Nitroglycerin Spray', 'Adenosine', 'Amiodarone',
        'Cardizem', 'magnesium Sulfate', 'Glucose Paste', 'Glucagon',
        'D50', 'D25', 'D10', 'Ondansetron', 'Solumedrol', 'Dexamethasone',
        'Benadryl', 'Epinephrine 1:1000', 'Epinephrine 1:10,000', 'Epinephrine 1:100,000', 'Lidocaine',
        'Albuterol', 'Ipratropium Bromide', 'Sodium Bicarb', 'Calcium Chloride',
        'Midazolam', 'Diazepam', 'Lorazepam', 'Ketamine', 'Hydroxocobalamin',
        'Sodium Thiosulfate'
      ],
      telMedName: 'ask',
      telMedDose: null,
      telMedUnit: 'mg',
      telMedRoute: 'iv-drip',
      hide: false
    }
  },
  watch: {
    medRestart () {
      if (this.medRestart === true) {
        this.telMedName = 'ask'
        this.telMedDose = null
        this.telMedUnit = 'mg'
        this.telMedRoute = 'iv-drip'
      }
    },
    telMedName () {
      if (this.telMedName === 'ns-kvo' || this.telMedName === 'ns-250' || this.telMedName === 'ns-1000') {
        this.hide = true
        this.telMedDose = 1000
      } else {
        this.hide = false
        this.telMedDose = null
      }
      if (this.telMedName !== 'ask') {
        this.$refs.name.style.borderColor = 'transparent'
      }
    }
  },
  methods: {
    telemetryData () {
      if (this.telMedName === 'ask') {
        this.$refs.name.style.borderColor = '#d32732'
      } else if (this.telMedDose === null) {
        this.$refs.dose.style.borderColor = '#d32732'
      } else if (this.telMedDose <= 0) {
        this.$refs.dose.style.borderColor = '#d32732'
      } else {
        this.$emit('tel-data', this.telMedName, this.telMedDose, this.telMedUnit, this.telMedRoute)
        this.telMedName = 'ask'
        this.telMedDose = null
        this.telMedUnit = 'mg'
        this.telMedRoute = 'iv-drip'
      }
    },
    valid () {
      if (this.telMedDose !== null) {
        this.$refs.dose.style.borderColor = 'transparent'
      }
    }
  }
}
</script>

